
.App {
  width: 100%;
  height: 100vh;
}

.mapTool {
  position: absolute;
  top: 10px;
  left: 10px;
}
.mapTool button {
  border: 1px solid #aaa;
  color: #aaa;
  background: #fff;
  margin-right: 10px;
}
.mapTool button.active {
  border: 1px solid #999;
  color: #000;

}

textarea {
  margin-top: 20px;
}



